"use client"

import type React from "react"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card } from "@/components/ui/card"
import { X, AlertCircle, Loader2 } from "lucide-react"

interface StockUpdateModalProps {
  isOpen: boolean
  product: {
    id: number
    nom: string
    quantite: number
    unite: string
  }
  onClose: () => void
  onUpdate: (productId: number, newQuantity: number) => Promise<void>
}

export default function StockUpdateModal({ isOpen, product, onClose, onUpdate }: StockUpdateModalProps) {
  const [quantityChange, setQuantityChange] = useState("")
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState("")
  const [operationType, setOperationType] = useState<"add" | "remove" | "manual">("add")

  const newQuantity = calculateNewQuantity()

  function calculateNewQuantity() {
    if (!quantityChange || isNaN(Number(quantityChange))) return product.quantite
    const change = Number(quantityChange)
    return product.quantite + change
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setError("")

    if (!quantityChange || isNaN(Number(quantityChange))) {
      setError("Veuillez entrer une quantité valide")
      return
    }

    const change = Number(quantityChange)
    if (operationType === "remove" && product.quantite + change < 0) {
      setError(`Vous ne pouvez pas retirer plus que la quantité en stock (${product.quantite})`)
      return
    }

    setLoading(true)
    try {
      await onUpdate(product.id, change)
      setQuantityChange("")
      onClose()
    } catch (error) {
      console.error("Erreur lors de la mise à jour du stock:", error)
      setError("Erreur lors de la mise à jour du stock")
    } finally {
      setLoading(false)
    }
  }

  const handleQuickAdd = (amount: number) => {
    setQuantityChange(amount.toString())
    setOperationType("add")
    setError("")
  }

  const handleQuickRemove = (amount: number) => {
    if (product.quantite - amount < 0) {
      setError(`Stock insuffisant. Vous avez seulement ${product.quantite} ${product.unite}`)
      return
    }
    setQuantityChange((-amount).toString())
    setOperationType("remove")
    setError("")
  }

  if (!isOpen) return null

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
      <Card className="w-full max-w-md p-6 relative shadow-xl">
        <button
          onClick={onClose}
          className="absolute top-4 right-4 text-gray-500 hover:text-gray-700 transition"
        >
          <X className="w-5 h-5" />
        </button>

        <h2 className="text-2xl font-bold text-gray-900 mb-2">Mettre à jour le stock</h2>
        <p className="text-gray-600 mb-6 font-medium">{product.nom}</p>

        {/* Stock actuel */}
        <div className="bg-gray-50 p-4 rounded-md mb-6 border border-gray-200">
          <p className="text-sm text-gray-600 mb-1">Stock actuel</p>
          <p className="text-2xl font-bold text-gray-900">
            {product.quantite} {product.unite}
          </p>
        </div>

        {/* Message d'erreur */}
        {error && (
          <div className="mb-4 p-3 bg-red-50 border border-red-200 rounded-md flex items-start gap-2">
            <AlertCircle className="w-5 h-5 text-red-600 flex-shrink-0 mt-0.5" />
            <p className="text-sm text-red-800">{error}</p>
          </div>
        )}

        <form onSubmit={handleSubmit} className="space-y-4">
          {/* Sélection du type d'opération */}
          <div className="grid grid-cols-3 gap-2">
            <button
              type="button"
              onClick={() => {
                setOperationType("add")
                setError("")
              }}
              className={`py-2 px-3 rounded-md font-medium text-sm transition ${
                operationType === "add"
                  ? "bg-green-100 text-green-700 border-2 border-green-500"
                  : "bg-gray-100 text-gray-700 border-2 border-gray-200"
              }`}
            >
              Ajouter (+)
            </button>
            <button
              type="button"
              onClick={() => {
                setOperationType("remove")
                setError("")
              }}
              className={`py-2 px-3 rounded-md font-medium text-sm transition ${
                operationType === "remove"
                  ? "bg-red-100 text-red-700 border-2 border-red-500"
                  : "bg-gray-100 text-gray-700 border-2 border-gray-200"
              }`}
            >
              Retirer (-)
            </button>
            <button
              type="button"
              onClick={() => {
                setOperationType("manual")
                setError("")
              }}
              className={`py-2 px-3 rounded-md font-medium text-sm transition ${
                operationType === "manual"
                  ? "bg-blue-100 text-blue-700 border-2 border-blue-500"
                  : "bg-gray-100 text-gray-700 border-2 border-gray-200"
              }`}
            >
              Manuelle
            </button>
          </div>

          {/* Champ de saisie */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-2">
              {operationType === "add" ? "Quantité à ajouter" : operationType === "remove" ? "Quantité à retirer" : "Nouvelle quantité"}
            </label>
            <Input
              type="number"
              value={quantityChange}
              onChange={(e) => {
                setQuantityChange(e.target.value)
                setError("")
              }}
              placeholder={operationType === "manual" ? "ex: 150" : "ex: 50"}
              step="0.01"
              required
              disabled={loading}
            />
          </div>

          {/* Aperçu du nouveau stock */}
          {quantityChange && (
            <div className="bg-blue-50 p-3 rounded-md border border-blue-200">
              <p className="text-sm text-blue-600">Nouveau stock après la modification</p>
              <p className="text-lg font-bold text-blue-700">
                {newQuantity.toFixed(2)} {product.unite}
              </p>
            </div>
          )}

          {/* Boutons rapides */}
          <div>
            <p className="text-xs font-medium text-gray-600 mb-2">Raccourcis rapides</p>
            <div className="grid grid-cols-4 gap-2">
              {operationType === "add" ? (
                <>
                  <button
                    type="button"
                    onClick={() => handleQuickAdd(10)}
                    className="py-1 px-2 bg-green-100 text-green-700 rounded text-xs font-medium hover:bg-green-200 transition"
                  >
                    +10
                  </button>
                  <button
                    type="button"
                    onClick={() => handleQuickAdd(25)}
                    className="py-1 px-2 bg-green-100 text-green-700 rounded text-xs font-medium hover:bg-green-200 transition"
                  >
                    +25
                  </button>
                  <button
                    type="button"
                    onClick={() => handleQuickAdd(50)}
                    className="py-1 px-2 bg-green-100 text-green-700 rounded text-xs font-medium hover:bg-green-200 transition"
                  >
                    +50
                  </button>
                  <button
                    type="button"
                    onClick={() => handleQuickAdd(100)}
                    className="py-1 px-2 bg-green-100 text-green-700 rounded text-xs font-medium hover:bg-green-200 transition"
                  >
                    +100
                  </button>
                </>
              ) : operationType === "remove" ? (
                <>
                  <button
                    type="button"
                    onClick={() => handleQuickRemove(5)}
                    className="py-1 px-2 bg-red-100 text-red-700 rounded text-xs font-medium hover:bg-red-200 transition"
                  >
                    -5
                  </button>
                  <button
                    type="button"
                    onClick={() => handleQuickRemove(10)}
                    className="py-1 px-2 bg-red-100 text-red-700 rounded text-xs font-medium hover:bg-red-200 transition"
                  >
                    -10
                  </button>
                  <button
                    type="button"
                    onClick={() => handleQuickRemove(25)}
                    className="py-1 px-2 bg-red-100 text-red-700 rounded text-xs font-medium hover:bg-red-200 transition"
                  >
                    -25
                  </button>
                  <button
                    type="button"
                    onClick={() => handleQuickRemove(50)}
                    className="py-1 px-2 bg-red-100 text-red-700 rounded text-xs font-medium hover:bg-red-200 transition"
                  >
                    -50
                  </button>
                </>
              ) : null}
            </div>
          </div>

          {/* Boutons d'action */}
          <div className="flex gap-3 pt-2">
            <Button
              type="submit"
              disabled={loading || !quantityChange}
              className="flex-1 bg-green-600 hover:bg-green-700 text-white flex items-center justify-center gap-2"
            >
              {loading && <Loader2 className="w-4 h-4 animate-spin" />}
              {loading ? "Mise à jour..." : "Confirmer"}
            </Button>
            <Button
              type="button"
              onClick={onClose}
              variant="outline"
              className="flex-1 text-gray-700"
              disabled={loading}
            >
              Annuler
            </Button>
          </div>
        </form>
      </Card>
    </div>
  )
}
