"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Plus, Edit2, Trash2, Package, AlertCircle, Loader2 } from "lucide-react"
import { getUser } from "@/lib/auth"
import StockUpdateModal from "@/components/stock-update-modal"
import Link from "next/link"

interface Product {
  id: number
  nom: string
  quantite: number
  unite: string
  prix: number
  dateExpiration: string
  statut: string
}

export default function FarmerProductsPage() {
  const router = useRouter()
  const [user, setUser] = useState(null)
  const [products, setProducts] = useState<Product[]>([])
  const [loading, setLoading] = useState(true)
  const [selectedProduct, setSelectedProduct] = useState<Product | null>(null)
  const [showStockModal, setShowStockModal] = useState(false)
  const [filterStatus, setFilterStatus] = useState("tous")
  const [searchTerm, setSearchTerm] = useState("")
  const [error, setError] = useState("")
  const [successMessage, setSuccessMessage] = useState("")

  useEffect(() => {
    const currentUser = getUser()
    if (!currentUser || currentUser.role !== "agriculteur") {
      router.push("/login")
      return
    }
    setUser(currentUser)
    fetchProducts()
  }, [router])

  const fetchProducts = async () => {
    try {
      const response = await fetch("http://localhost:8080/api/v1/recoltes")
      const data = await response.json()
      setProducts(data)
    } catch (error) {
      console.error("Erreur lors du chargement des produits:", error)
    } finally {
      setLoading(false)
    }
  }

  const handleDelete = async (id: number) => {
    if (!confirm("Êtes-vous sûr de vouloir supprimer ce produit?")) return

    try {
      const response = await fetch(`http://localhost:8080/api/v1/recoltes/${id}`, {
        method: "DELETE",
      })
      if (response.ok) {
        setProducts(products.filter((p) => p.id !== id))
        setSuccessMessage("Produit supprimé avec succès!")
        setTimeout(() => setSuccessMessage(""), 3000)
      } else {
        setError("Erreur lors de la suppression du produit")
      }
    } catch (error) {
      console.error("Erreur lors de la suppression:", error)
      setError("Erreur lors de la suppression du produit")
    }
  }

  const handleStockUpdate = async (productId: number, quantityChange: number) => {
    try {
      const response = await fetch(
        `http://localhost:8080/api/v1/recoltes/${productId}/stock?quantite=${quantityChange}`,
        {
          method: "PATCH",
        },
      )
      if (response.ok) {
        const updatedProduct = await response.json()
        setProducts(products.map((p) => (p.id === productId ? { ...p, quantite: updatedProduct.quantite } : p)))
      }
    } catch (error) {
      console.error("Erreur lors de la mise à jour du stock:", error)
      throw error
    }
  }

  const openStockModal = (product: Product) => {
    setSelectedProduct(product)
    setShowStockModal(true)
  }

  const filteredProducts = products.filter((product) => {
    const matchesSearch = product.nom.toLowerCase().includes(searchTerm.toLowerCase())
    const matchesStatus = filterStatus === "tous" || product.statut === filterStatus
    return matchesSearch && matchesStatus
  })

  const getStats = () => {
    const total = products.length
    const disponibles = products.filter((p) => p.statut === "Disponible").length
    const indisponibles = products.filter((p) => p.statut === "Indisponible").length
    const totalValue = products.reduce((acc, p) => acc + p.prix * p.quantite, 0)
    return { total, disponibles, indisponibles, totalValue }
  }

  const stats = getStats()

  if (!user) return null

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-7xl mx-auto px-4 py-8">
        {/* En-tête */}
        <div className="flex justify-between items-center mb-8">
          <div>
            <h1 className="text-3xl font-bold text-gray-900">Mes Produits</h1>
            <p className="text-gray-600 mt-1">Gérez votre inventaire agricole</p>
          </div>
          <Link href="/farmer/products/new">
            <Button className="bg-green-600 hover:bg-green-700 flex items-center gap-2">
              <Plus className="w-5 h-5" />
              Ajouter un produit
            </Button>
          </Link>
        </div>

        {/* Messages d'erreur et de succès */}
        {error && (
          <div className="mb-6 p-4 bg-red-50 border border-red-200 rounded-md flex items-center gap-3">
            <AlertCircle className="w-5 h-5 text-red-600" />
            <p className="text-red-800">{error}</p>
            <button
              onClick={() => setError("")}
              className="ml-auto text-red-600 hover:text-red-800"
            >
              ✕
            </button>
          </div>
        )}

        {successMessage && (
          <div className="mb-6 p-4 bg-green-50 border border-green-200 rounded-md flex items-center gap-3">
            <svg className="w-5 h-5 text-green-600" fill="currentColor" viewBox="0 0 20 20">
              <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" />
            </svg>
            <p className="text-green-800">{successMessage}</p>
            <button
              onClick={() => setSuccessMessage("")}
              className="ml-auto text-green-600 hover:text-green-800"
            >
              ✕
            </button>
          </div>
        )}

        {/* Statistiques */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-8">
          <Card className="p-6 bg-white shadow-sm">
            <p className="text-gray-600 text-sm">Total de produits</p>
            <p className="text-3xl font-bold text-gray-900 mt-2">{stats.total}</p>
          </Card>
          <Card className="p-6 bg-green-50 shadow-sm border border-green-200">
            <p className="text-green-700 text-sm">Disponibles</p>
            <p className="text-3xl font-bold text-green-600 mt-2">{stats.disponibles}</p>
          </Card>
          <Card className="p-6 bg-red-50 shadow-sm border border-red-200">
            <p className="text-red-700 text-sm">Indisponibles</p>
            <p className="text-3xl font-bold text-red-600 mt-2">{stats.indisponibles}</p>
          </Card>
          <Card className="p-6 bg-blue-50 shadow-sm border border-blue-200">
            <p className="text-blue-700 text-sm">Valeur totale</p>
            <p className="text-3xl font-bold text-blue-600 mt-2">{stats.totalValue.toFixed(2)} DH</p>
          </Card>
        </div>

        {/* Recherche et filtrage */}
        <Card className="p-4 mb-6 shadow-sm">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <input
                type="text"
                placeholder="Rechercher par nom..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full px-4 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-green-500 focus:border-transparent outline-none"
              />
            </div>
            <div>
              <select
                value={filterStatus}
                onChange={(e) => setFilterStatus(e.target.value)}
                className="w-full px-4 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-green-500 focus:border-transparent outline-none bg-white"
              >
                <option value="tous">Tous les statuts</option>
                <option value="Disponible">Disponible</option>
                <option value="Indisponible">Indisponible</option>
                <option value="En attente">En attente</option>
              </select>
            </div>
          </div>
        </Card>

        {/* Liste des produits */}
        {loading ? (
          <div className="text-center py-12">
            <Loader2 className="w-8 h-8 animate-spin text-green-600 mx-auto mb-4" />
            <p className="text-gray-600">Chargement des produits...</p>
          </div>
        ) : filteredProducts.length === 0 ? (
          <Card className="p-12 text-center shadow-sm">
            <Package className="w-12 h-12 text-gray-400 mx-auto mb-4" />
            <p className="text-gray-600 text-lg">
              {searchTerm || filterStatus !== "tous" ? "Aucun produit trouvé" : "Aucun produit"}
            </p>
            <p className="text-gray-500 text-sm mt-2">
              {searchTerm || filterStatus !== "tous"
                ? "Essayez de modifier votre recherche"
                : "Commencez par ajouter votre premier produit"}
            </p>
          </Card>
        ) : (
          <div className="grid gap-4">
            {filteredProducts.map((product) => (
              <Card key={product.id} className="p-6 hover:shadow-lg transition-shadow">
                <div className="flex items-center justify-between gap-4 flex-wrap">
                  <div className="flex-1 min-w-0">
                    <h3 className="text-lg font-semibold text-gray-900 truncate">{product.nom}</h3>
                    <div className="grid grid-cols-2 md:grid-cols-5 gap-3 mt-3 text-sm text-gray-600">
                      <div>
                        <p className="text-xs text-gray-500 mb-1">Quantité</p>
                        <p className="font-medium text-gray-900">
                          {product.quantite} {product.unite}
                        </p>
                      </div>
                      <div>
                        <p className="text-xs text-gray-500 mb-1">Prix unitaire</p>
                        <p className="font-medium text-gray-900">{product.prix.toFixed(2)} DH</p>
                      </div>
                      <div>
                        <p className="text-xs text-gray-500 mb-1">Total</p>
                        <p className="font-medium text-blue-600">{(product.prix * product.quantite).toFixed(2)} DH</p>
                      </div>
                      <div>
                        <p className="text-xs text-gray-500 mb-1">Expiration</p>
                        <p className="font-medium text-gray-900">
                          {new Date(product.dateExpiration).toLocaleDateString("fr-FR")}
                        </p>
                      </div>
                      <div>
                        <p className="text-xs text-gray-500 mb-1">Statut</p>
                        <span
                          className={`inline-block px-3 py-1 rounded-full text-xs font-medium ${
                            product.statut === "Disponible"
                              ? "bg-green-100 text-green-800"
                              : product.statut === "Indisponible"
                                ? "bg-red-100 text-red-800"
                                : "bg-yellow-100 text-yellow-800"
                          }`}
                        >
                          {product.statut}
                        </span>
                      </div>
                    </div>
                  </div>
                  <div className="flex gap-2 flex-wrap justify-end">
                    <Button
                      size="sm"
                      onClick={() => openStockModal(product)}
                      className="flex items-center gap-2 bg-blue-600 hover:bg-blue-700 text-white whitespace-nowrap"
                    >
                      <Package className="w-4 h-4" />
                      Stock
                    </Button>
                    <Link href={`/farmer/products/${product.id}/edit`}>
                      <Button variant="outline" size="sm" className="flex items-center gap-2">
                        <Edit2 className="w-4 h-4" />
                        Modifier
                      </Button>
                    </Link>
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => handleDelete(product.id)}
                      className="flex items-center gap-2 text-red-600 hover:text-red-700 hover:bg-red-50"
                    >
                      <Trash2 className="w-4 h-4" />
                      Supprimer
                    </Button>
                  </div>
                </div>
              </Card>
            ))}
          </div>
        )}
      </div>

      {selectedProduct && (
        <StockUpdateModal
          isOpen={showStockModal}
          product={selectedProduct}
          onClose={() => setShowStockModal(false)}
          onUpdate={handleStockUpdate}
        />
      )}
    </div>
  )
}
